import {
	loginUser
} from '../../api/api.js'

export default {
	data() {
		return {
			loginForm: {
				userName: null,
				passWord: null
			}
		}
	},
	mounted() {},
	methods: {
		commitLogForm() {
			if (this.loginForm.userName && this.loginForm.passWord) {
				let params = {
					userName: this.loginForm.userName,
					passWord: this.loginForm.passWord
				};
				this.$store.dispatch('Login', params).then((res) => {
						if (res.data.status === 'success') {
							uni.showToast({
								icon: 'none',
								title: '登录成功'
							});
							uni.navigateBack({
								delta: 1
							})
						} else {
							uni.showToast({
								icon: 'none',
								title: '账号或密码错误'
							});
						}
					})
					.catch((error) => {})
			} else {}
		}
	}
}
